.App {
  position: absolute;
  width: 100%;
  min-height: 100%;
  display: grid;
  grid-template-rows: 1fr auto;
}
.App.full-height {
  height: 100%;
}
.App.App-BOOT {
  user-select: none;
  background: #08141e;
  grid-template-rows: auto;
}
.App.App-BOOT .Output {
  display: flex;
  justify-self: center;
  align-self: center;
  max-width: 512px;
}
.App.App-BOOT .TopBar {
  display: none;
}
.App.App-RUN {
  user-select: none;
  background: #08141e;
  grid-template-rows: 1.5rem auto;
}
.App.App-RUN .Output {
  display: flex;
  max-width: 512px;
  justify-self: center;
  align-self: center;
}
.App.App-CODE {
  user-select: none;
  background: #08141e;
  grid-template-rows: 1.5rem auto;
}
.App.App-CODE .Output {
  display: flex;
  top: calc(3rem + 2px);
  right: 1.5rem;
  width: calc(128px * 3);
  height: calc(100vh - 4.5rem - 2px);
}
